import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function usePsychologyConditions() {
  const psychologyConditions = ref([]);
  const fetchPsychologyConditions = async () => {
    const { data } = await InfoService.fetchPsychologyConditions();
    psychologyConditions.value = data;
  };
  onMounted(async () => {
    await fetchPsychologyConditions();
  });

  return {
    psychologyConditions,
  };
}
